import React from 'react'

const defaultLocale = 'ru';
const IString = (props) => {
  const locale = typeof window !== 'undefined' ? (window.localStorage.getItem('mib32.locale') || defaultLocale) : defaultLocale;

  let content;
  
  if (props[locale]) {
    content = props[locale]
  } else {
    content = props[defaultLocale]
  }
  

  return (
    <span>{content}</span>
  )
}

export default IString